.nav{
    display: flex;
    list-style-type: none;
    padding:  0;

}

.nav li{
    width: 18%
}

.nav li + li {
    margin-left: .525em;
}

.nav li > a {
    text-align: center;
}

.nav li > a.active {
    text-decoration: underline;
}


